
.nav {
  &__list {

      margin: 0;
      padding: 0;
      list-style: none;

      display: flex;
      flex-direction: column;
      gap: 32px;

    }

    &__link {
    position: relative;
    color: inherit;
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;



  &::after {

    content: '';
    position: absolute;
    bottom: -8px;
    height: 1px;
    display: block;
    width: 100%;
    background-color: $text-color;

    transition: transform 0.3s;
    transform: scale(0);
    transform-origin: left;

  }

  &:hover::after {

    transform: scale(1);

  }
 }
}
