
.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: $background-color;
  pointer-events: none;
  font-weight: 700;

  &__top {
    margin-bottom: 32px;

  /*  @include onTablet {
     margin-bottom:0;

    }*/
  }

  &__nav {
    margin-bottom: 48px; // відступ вниз
  }

  &__content {
    padding-inline: 20px;
  }

  &_phone_number {
    color: inherit;
    text-decoration: none;
    display: block;
    font-size: 16px;
    line-height: 23px;
    letter-spacing: 2px;
    margin-bottom: 16px;
  }

  &_call_to_order {
    color: inherit;
    text-decoration: none;
    display: block;
    font-size: 16px;
    line-height: 23px;
    letter-spacing: 2px;
    padding-bottom: 4px;
    border-bottom: 1px solid $text-color;
    width: fit-content;
  }
}
