@mixin hover($property, $toValue) {
  transition: #{$property} 0.3s;

  &:hover {
    #{$property}: #{$toValue};
  }
}

.top-bar {
  padding: 24px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__icons {
    display: flex;
    gap: 24px;
  }

  &__logo {
    display: flex;

   @include hover(transform, scale(1.2));

    img {
      width: 152px;
      height: auto;
    }

  }
}
