

.page {
  font-family: Manrope, sans-serif;
  color: $text-color;

  scroll-behavior: smooth;

    .page__menu:target {
        opacity: 1;
        pointer-events: all;
        transform: translateX(0);
      }
  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    opacity: 0;


  }
}

