
.header {
  background-image: url(/images/Image.png);
  height: 100vh;
  background-size: cover;
  background-position: center;
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

&__title {
  margin: 0;
  padding: 0 20px 40px;
  width: 280px;
  text-align: left;
  color: $background-color;
  font-size: 36px;
  line-height: 36px;
  font-weight: 700;
}
}
